@import (less) 'color.less';
.user-button-container {
    padding: 0;
}

[class*=" ico-"]:before {
    vertical-align: middle;
}

.user-button {
    margin: 0;
    a {
        color: @orange;
        display: table;
        padding: 0em 1.250em 0em 1.250em;
        margin: 1.250em 0 1.250em 0;
        text-decoration: none;
        cursor: pointer;
        width: 19.375em;
        height: 2.813em;
        &:hover,
        &:focus,
        &:active {
            background-color: @orange;
            color: @darkest_gray;
            }
        table {
            width: 100%;
            display: table-cell;
            vertical-align: middle;
        }
    }
}

.user-image-container {
    
    div {
        vertical-align: middle;
        font-size: 1.750em !important;
        margin: 0 !important;
    }
    img {
        position: relative;
        border-radius: 50%;
        width: 100%;
    }
}

.user-info {
    padding-left: 0.625em;
    vertical-align: middle;
    line-height: 1;
    font-weight: normal;
}

.user-info-subtitle {
    display: inline-block;
    font-size: 0.875em;
    font-weight: normal;
}

:host-context(.contrasto-on) {
  .user-button {
      a {
          color: @white;
          &:hover,
          &:focus,
          &:active {
              background-color: @white;
              color: @black;
          }
      }
  }
}